@import 'variables.scss';

.pdfViewer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  // z-index: 11;
  height:50%;
  .demo{
    margin-bottom: 15px;
  }
  .links{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 55px;
    height: 100%;
    justify-content: space-evenly;
    .linkDiv-Grey{
      opacity: .5
    }
    .linkDiv{
      z-index: 5;
      align-content: center;
      transition: all .2s ease-in-out;
      img{
        height: 60px;
      }
      #demoIcon{
        height: 80px;
      }
      &:hover {
        transform: scale(1.2, 1.2);
        cursor: pointer;
        color: white;
      }
    }
  }
}



.pdfViewer-modal{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  font-size: $font-md;
  font-family: light;
  text-align: center;
  i.teal.icon {
    color: $brightTeal!important;
  }
  .downloadButton{
    margin-bottom: 10px;
  }
  .pageNumber{
    color: $brightTeal;
  }
  .pageturnerButton{
    height: 20px;
    width: 20px;
    &:hover {
      color: $brightTeal;
    }
  }
  .exitButton{
    margin-right: 0px;
    margin-top: 25px;
    color: $brightTeal;
  }
}
